import React from "react";
import { Dots } from 'react-activity';
import { connect } from "react-redux";

class ThirdStep extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    onPreviousStepPress = () => {
        window.scrollTo(0,0);
        this.props.previousStep();
    }

    onNextStepPress = () => {
        this.setState({isLoading:true});
        setTimeout(() => {
            this.setState({isLoading:false})
            window.scrollTo(0,0);
            this.props.nextStep()
        }, 500)
    }

    render () {
        return (
        <div className="step">
            <h2 className="step-title">Recap</h2>
            <p>You will find here all the recap of your order. Please be sure everything is ok before paying.</p>
            <div className="contained">
                <table className="table">
                    <thead>
                        <tr>
                        <th scope="col">#</th>
                        <th scope="col">First Name</th>
                        <th scope="col">Last Name</th>
                        <th scope="col">Ticket type</th>
                        <th scope="col">Reduced Price</th>
                        <th scope="col">Cost</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                        <th scope="row">1</th>
                        <td>Mark</td>
                        <td>Otto</td>
                        <td>Half-day</td>
                        <td>Yes</td>
                        <td>10€</td>
                        </tr>
                        <tr>
                        <th scope="row">2</th>
                        <td>Jacob</td>
                        <td>Thornton</td>
                        <td>@fat</td>
                        </tr>
                        <tr>
                        <th scope="row">3</th>
                        <td>Larry</td>
                        <td>the Bird</td>
                        <td>@twitter</td>
                        </tr>
                    </tbody>
                </table>
                <table className="table">
                    <thead className="thead-light">
                    <tr>
                        <th scope="col">Number of tickets</th>
                        <th scope="col">Total Price (VAT included)</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <th scope="row">4</th>
                        <td>133€</td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div className="form-group nav-line">
                <button className="btn btn-secondary" onClick={this.onPreviousStepPress}>Previous</button>
                <button className="btn btn-primary" onClick={this.onNextStepPress}>{this.state.isLoading ? <Dots color="white" /> : "Order"}</button>
            </div>
        </div>
        )
    }
}

export default connect(state => state) (ThirdStep)